body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 0px;
}

ul{
    margin-right: 16px;
    position: center;
    list-style-type: none;
    margin-left: -18px;
}

.verticalItem{
    background-color: white;
    margin-bottom: 16px;
    margin-top: 16px;
    display: block;
    width: 100%
}

.greeting{
    margin-left: 16px;
    margin-top:16px
}

.label{
    margin-right: 8px;
}

.material-switch {
    vertical-align: middle;
    margin-left: 4px;
}

.verticalText{
    color: #1e2322;
    height: 32px;
    text-decoration: none;
}

#barcont{
    background-color: white;
    position: absolute;
    top: 6%;
    left: 0;
    height: 94%;
    width: 100%;
}

#SBbutton {
    font-palette: light;
    float: left;
    font-size: 24px;
    font-weight: bold;
    height: 34px;
    margin-right: -1px;
    margin-top: -6px;
    padding: 0;
    text-align: center;
}
#SBheader{
    font-palette: light;
    float: left;
    font-size: 24px;
    font-weight: bold;
    height: 34px;
    margin-right: -1px;
    margin-top: -6px;
    padding: 0;
    text-align: center;
}

.upperBar {
    position: page;
    background-color: rgb(3, 169, 244);
    color: white;
    padding-bottom:34px;
    padding-top:10px;
    padding-left: 18px;
    padding-right: 18px;
    font-size: 1.5em;
}

.content{
    font: 14px "Century Gothic", Futura, sans-serif;
    margin-top: 8px;
    margin-left: 16px;
    height:88%;
    width: 96%;

    border: 1px solid red;
}

.container{
    border: 1px solid red;
    height: 100%;
}
.display-linebreak {
    white-space: pre;
}
.image{
    border: 1px solid red;

}
#settingsGear{
    color: white;
    margin-bottom: 4px;
    margin-top: -3px;
    position: center;
    height:30px;
    float: right;
}



